.contact__container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  column-gap: 30px;

  @include vp-767 {
    flex-direction: column;
    row-gap: 40px;
  }
}

.contact__map {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1 auto;

  iframe {
    border: none;

    @include vp-1023 {
      width: 324px;
      height: 335px;
    }

    @include vp-374 {
      width: 290px;
      height: 316px;
    }
  }
}

.contact__info {
  max-width: 500px;

  @include vp-767 {
    max-width: 324px;
  }
}

.contact__text {
  margin: 0;

  &--title {
    margin-bottom: 40px;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
  }

  &--address {
    position: relative;
    margin-bottom: 25px;
    padding-left: 28px;

    &::before {
      position: absolute;
      top: 50%;
      left: 0;
      content: "";
      width: 18px;
      height: 22px;
      background: url("../img/sprite/icon-location.svg") no-repeat center;
      transform: translate(0, -50%);

      @include vp-767 {
        top: 5px;
        transform: none;
      }
    }
  }

  &--phone {
    a {
      position: relative;
      margin-bottom: 25px;
      padding-left: 28px;
      white-space: nowrap;

      &::before {
        position: absolute;
        top: 50%;
        left: 0;
        content: "";
        width: 18px;
        height: 18px;
        background: url("../img/sprite/icon-phone.svg") no-repeat center;
        transform: translate(0, -50%);
      }
    }
  }
}
